.header{
    background-color: #fff;
    height: 60px;
    width: 100%;
    .header_box{
        height: 100%;
        padding: 5px 10px;
        .header_logo{
            float: left;
            width: 40%;
            text-align: center;
            height: 40px;
            line-height: 40px;
            font-size: 32px;
            img{
                cursor:pointer
            }
        }
        .header_nav{
            float: left;
            width: 60%;    
            height: 40px;
            line-height: 40px;
            span{
                padding: 0 30px;
                font-size: 16px;
                display: inline-block;
                height: 100%;
                cursor:pointer
            }
            span:hover{
                color: #1890ff;
            }
        }
    }
}